@import url(https://cdnjs.cloudflare.com/ajax/libs/normalize/8.0.1/normalize.min.css);
@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@400;500;700&display=swap);
.coub__layout{display:grid;grid-template-rows:repeat(3, 1fr);grid-template-columns:1fr;place-items:center;height:100vh}.coub__title{text-align:center;font-size:26px}@media screen and (min-width: 550px){.coub__title{font-size:32px}}.coub__next-button-layout{text-align:center}.coub__next-button{all:unset;box-sizing:border-box;width:165px;height:40px;border:7px solid #c0bebe;border-radius:30px;background:#c0bebe;font-family:inherit;font-size:20px;font-weight:500;cursor:pointer}@media screen and (min-width: 550px){.coub__next-button{width:260px;height:70px;font-size:24px}}.coub__next-button:hover,.coub__next-button:focus,.coub__next-button:active{background:#a1a0a0}

html{box-sizing:border-box;font-family:'Roboto', sans-serif}*,*::after,*::before{box-sizing:inherit}

